@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
:root[data-theme='dark']{--background-color-main: hsl(207, 26%, 17%);--background-color-element: hsl(209, 23%, 22%);--box-shadow-element: hsl(208, 26%, 13%);--hover-element: hsl(210, 27%, 20%);--text-color: hsl(0, 0%, 100%)}:root[data-theme='light']{--background-color-main: hsl(0, 0%, 98%);--background-color-element: hsl(0, 0%, 100%);--box-shadow-element: hsl(0, 9%, 90%);--hover-element: hsl(0, 37%, 97%);--text-color: hsl(200, 15%, 8%)}*,*::before,*::after{margin:0;padding:0;box-sizing:border-box}html{font-family:"Nunito Sans",sans-serif;font-size:14px;color:var(--text-color)}body{background-color:var(--background-color-main)}.container{margin:0 auto;max-width:1300px}::-webkit-scrollbar{width:15px}::-webkit-scrollbar-thumb{background:var(--text-color)}::-webkit-scrollbar-thumb:hover{background:#b3afb3}::-webkit-scrollbar-track{background:var(--box-shadow-element)}.header{background-color:var(--background-color-element);box-shadow:0 0 10px var(--box-shadow-element)}.header__container{display:flex;justify-content:space-between;padding:25px 15px 30px}.header__title{font-size:1rem}.header__btn{font-family:"Nunito Sans",sans-serif;font-weight:600;color:var(--text-color);outline:none;border-style:none;background-color:transparent;cursor:pointer}.header__btn:hover .header__btn-icon{-webkit-transform:rotate(calc(-30deg + 360deg)) scale(1.3);transform:rotate(calc(-30deg + 360deg)) scale(1.3)}.header__btn-icon{margin-right:5px;-webkit-transform:rotate(-30deg);transform:rotate(-30deg);transition:0.2s}@media screen and (min-width: 360px){.header__btn-icon{margin-right:10px}}@media screen and (min-width: 800px){.header__container{padding:20px 10px 25px}.header__title{font-size:1.5rem}}.content__container{padding:0 10px}.panel{display:flex;flex-direction:column;margin:20px 0}.panel__input,.panel__select{padding:10px;font-family:"Nunito Sans",sans-serif;font-size:11.2px;color:var(--text-color);background-color:var(--background-color-element);outline:none;border-style:none;border-radius:3px;box-shadow:0 0 10px var(--box-shadow-element);transition:0.1s}.panel__input:hover,.panel__select:hover{background-color:var(--hover-element)}.panel__label{position:relative;margin-bottom:30px}.panel__icon{position:absolute}.panel__icon--search{top:50%;left:10px;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.panel__icon--clear{display:none;top:50%;right:10px;color:red;-webkit-transform:translateY(-60%) scale(1.5);transform:translateY(-60%) scale(1.5);cursor:pointer}.panel__icon--clear.active{display:block}.panel__input{padding-left:30px;padding-right:35px;width:100%}.panel__input::-webkit-input-placeholder{color:var(--text-color)}.panel__input:-ms-input-placeholder{color:var(--text-color)}.panel__input::placeholder{color:var(--text-color)}.panel__select{width:50%;cursor:pointer}@media screen and (min-width: 360px){.panel__input,.panel__select{font-size:14px}.panel__icon--search{left:15px}.panel__icon--clear{right:15px}.panel__input{padding-left:40px}}@media screen and (min-width: 800px){.panel{flex-direction:row;justify-content:space-between;margin:30px 0}.panel__label{flex-basis:40%;margin-bottom:0}.panel__select{width:auto}}.results{display:grid;margin-bottom:50px;padding:0 10px;grid-gap:50px;gap:50px}@media screen and (min-width: 360px){.results{padding:0 20px}}@media screen and (min-width: 460px){.results{grid-template-columns:repeat(2, minmax(40%, 50%))}}@media screen and (min-width: 800px){.results{grid-template-columns:repeat(3, minmax(30%, calc(100% / 3)));padding:0}}@media screen and (min-width: 1024px){.results{grid-template-columns:repeat(4, minmax(20%, 25%))}}.card{background-color:var(--background-color-element);box-shadow:0 0 10px var(--box-shadow-element);border-radius:5px}.card__wrap{text-decoration:none;color:var(--text-color)}.card__image-wrap{aspect-ratio:3/2}.card__image{width:100%;height:100%}.card__details-wrap{padding:23px 20px}.card__title{margin-bottom:15px;font-size:1.2rem}.card__detail{margin-bottom:5px;font-weight:600}.card__detail-span{margin-left:5px;font-weight:300}@media screen and (min-width: 800px){.card{transition:0.2s}.card:hover{background-color:var(--hover-element);box-shadow:0 0 50px 10px var(--box-shadow-element);-webkit-transform:scale(1.05);transform:scale(1.05)}}.details-wrap__img{aspect-ratio:3/2}@media screen and (min-width: 360px){.details-wrap{padding:0 20px}}@media screen and (min-width: 800px){.details-wrap{display:flex;justify-content:space-between}.details-wrap__img,.details-wrap__detail{flex-basis:45%}}@media screen and (min-width: 1024px){.details-wrap__detail{display:flex;justify-content:space-between;flex-wrap:wrap}}.detail__img{width:100%;height:100%}.detail__name{margin:25px 0 20px}.detail__info{margin-bottom:6px;font-weight:600}.detail__info-span{margin-left:5px;font-weight:300}.detail__info-span--border{line-height:1.8rem}.detail__content-wrap:nth-child(2){margin-bottom:20px}.detail__content-wrap:nth-child(3){margin-bottom:20px}.detail__border-wrap{flex-basis:100%;display:flex;flex-wrap:wrap;align-items:flex-start;align-content:flex-start}.detail__border-title{flex-basis:100%;margin-bottom:10px}.detail__btn{display:inline-block;margin:0 5px 5px;padding:5px 15px;font-size:0.9rem;color:var(--text-color);text-decoration:none;outline-style:none;border-style:none;background-color:var(--background-color-element);box-shadow:0 0 10px var(--box-shadow-element);cursor:pointer;transition:0.2s}.detail__btn:hover{background-color:var(--hover-element)}.detail__btn--back{margin:30px 0 40px}@media screen and (min-width: 360px){.detail__btn{margin:0 10px 10px}.detail__btn--back{margin:30px 0 40px 20px}.detail__border-title{flex-basis:auto;margin-bottom:0}}@media screen and (min-width: 1024px){.detail__name{flex-basis:100%}}

